import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';

const MARGIN_TOP = mu.marginTop(false);
const MARGIN_BOTTOM = mu.marginBottom();

@observer
class Drawer extends Component {
    onCloseClick = (e) => {
        const { onCloseClick, drawer } = this.props;
        if (onCloseClick) {
            onCloseClick();
        } else {
            const { appState } = this.props.stores;
            if (drawer === 2) {
                appState.toggleDrawer2(false);
            } else {
                appState.toggleDrawer(false);
            }
        }
    }

    render() {
        const { appState } = this.props.stores;
        const { darkmode } = appState;
        const {
            children,
            classNames = 'fixed-bottom',
            width = '100vw',
            height = '75vh',
            drawer,
        } = this.props;
        return (<>
            {drawer === 2 ? <>
                <div
                    class='w-100 h-100 fixed-bottom'
                    style={`
                        height: calc(100vh) !important;
                        top: 0;
                        left: 0;
                        overflow-x: auto;
                        overflow-y: auto;
                        transition: .3s;
                        overscroll-behavior: contain;
                        z-index: 1000000;
                        background-color: rgba(0,0,0,.6);
                    `}
                    onClick={this.onCloseClick}
                />
            </> : <>
                <div
                    class='w-100 h-100 fixed-bottom'
                    style={`
                        height: calc(100vh) !important;
                        top: 0;
                        left: 0;
                        overflow-x: auto;
                        overflow-y: auto;
                        transition: .3s;
                        overscroll-behavior: contain;
                        z-index: 90000;
                        background-color: rgba(0,0,0,.6);
                    `}
                    onClick={this.onCloseClick}
                />
            </>}
            <div
                class={`${classNames} ${darkmode ? 'bg-darkmode' : 'bg-lightmode'} border rounded rounded-lg`}
                style={`
                    bottom: 0;
                    left: 0;
                    width: ${width};
                    height: ${height};
                    max-height: 90vh;
                    min-height: 10vh;
                    overflow: hidden;
                    z-index: ${drawer === 2 ? 1000010 : 90010};
                    border-radius: 5px 5px 0 0 !important;
                    border-top-width: 5px !important;
                `}
                onTouchmove={mu.captureAllEvents}
                onScroll={mu.captureAllEvents}
            >
                <button
                    class='btn btn-warning btn-sm float-right position-absolute'
                    style={`
                        top: 5px; 
                        right: 5px; 
                        z-index: ${drawer === 2 ? 1000100 : 90100};
                        `}
                    onClick={this.onCloseClick}
                >
                    <i class='fas fa-times' />
                </button>
                <div
                    class='h-100 overflow-auto bg-darkmode'
                    onTouchmove={mu.captureAllEvents}
                    onScroll={mu.captureAllEvents}    
                >
                    {children}
                </div>
            </div>
        </>);
    }
}

export default Drawer;
